import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)


// /
export default new Vuex.Store({
	// statedata this.$store.state.
	state:{
		num:1
	},
	// mutations this.$store.commit('',)
	mutations:{
		add(state){
			state.num++
		},
		sub(state){
			if(state.num>1){
				state.num--
			}
			
		}
	}
})


